* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  height: 60px;
  background-color: #ff8282;
}

.root {
  padding: 30px;
}

.tasks-container {
  max-width: 311px;
  flex-direction: column;
  gap: 15px;
  margin: 46px auto;
  display: flex;
}

.lastTaskAdd {
  opacity: 0;
  animation: 2s forwards show;
  scale: 0;
}

@keyframes show {
  from {
    opacity: 0;
    scale: 0;
  }

  to {
    opacity: 1;
    scale: 1;
  }
}

.noPendientesAnimation {
  margin: 30px auto;
  font-size: 25px;
  animation: 2s forwards noPendientes;
  scale: 1;
}

@keyframes noPendientes {
  0% {
    scale: 1;
  }

  25% {
    scale: 1.5;
  }

  50% {
    scale: 1.5;
  }

  75% {
    scale: .5;
  }

  100% {
    scale: 1;
  }
}

@media (min-width: 850px) {
  .form {
    max-width: 960px;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    gap: 30px;
  }

  .tasks-container {
    max-width: 960px;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0 15px;
    display: grid;
  }

  .tasks-container task-el {
    margin-top: 40px;
  }

  .noPendientesAnimation {
    text-align: center;
    margin: 250px auto;
    font-size: 40px;
    display: inline;
    position: absolute;
    left: 40px;
    right: 1px;
  }
}

